.App {
    font-family: sans-serif;
    text-align: center;
}

.Quiz {
    font-family: sans-serif;
    text-align: center;
}

.Desktop-1 {
    position: relative;
    width: 100%;
    height: 100%;
    background: #FFFFFF;
}

.header {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
}

.card {
    position: absolute;
    width: 380px;
    height: 512px;
    left: 530px;
    top: 114px;


    background: #EEEEEE;
    box-shadow: 0px 4px 64px rgba(0, 0, 0, 0.05);
    border-radius: 8px;
}

.card_results {
    position: absolute;
    width: 560px;
    height: 512px;
    left: 440px;
    top: 114px;

    background: #EEEEEE;
    box-shadow: 0px 4px 64px rgba(0, 0, 0, 0.05);
    border-radius: 8px;
}

.result_text {
    /* Результаты профориентационного тестирования */
    position: relative;
    width: 520px;
    left: 20px;
    top: 23px;

    font-family: Rubik, serif;
    font-style: normal;
    font-weight: bold;
    font-size: 26px;
    line-height: 31px;

    color: #000000;

}

.results {

    color: #FFFFFF;
    position: relative;
    top: 50px;
    left: 25px;
    border-radius: 8px;
    height: 40px;
    border: #EEEEEE;

    padding-top: 20px;
    padding-left: 10px;
}

.img {
    position: absolute;
    width: 364px;
    height: 364px;
    left: 538px;
    top: 122px;

    background: #FFFFFF;
    border-radius: 8px;
}

.card-text {

    position: absolute;
    width: 340px;
    height: 92px;
    left: 550px;
    top: 506px;

    font-family: Rubik, serif;
    font-style: normal;
    font-weight: bold;
    font-size: 26px;
    line-height: 31px;
    display: flex;
    align-items: center;

    color: #000000;

}

.grade {
    position: absolute;
    width: 380px;
    height: 64px;
    left: 530px;
    top: 646px;


    background: #EEEEEE;
    box-shadow: 0px 4px 64px rgba(0, 0, 0, 0.05);
    border-radius: 8px;
}

.stars {
    position: relative;
    font-size: 4.0rem;
    text-align: center;
    top: -16px;

}

#Rectangle-1 {

    width: 100%;
    height: 8%;

    background: #C4C4C4;

}

#Ellipse-1 {

    position: absolute;
    width: 2rem;
    height: 2rem;
    border-radius: 1rem;
    left: 90%;
    top: 1rem;

    background: #DDDDDD;
}

#Ellipse-2 {

    position: absolute;
    width: 2rem;
    height: 2rem;
    border-radius: 1rem;
    left: 85%;
    top: 1rem;

    background: #DDDDDD;
}
.fade {
    animation: fade-in-keyframes 1s;
}
@keyframes fade-in-keyframes {
    from {opacity: 0}
    to {opacity: 1}
}
.reverse {
    animation-direction: reverse;
}
.normal
{
    animation-direction: normal;
}
.particles{
    width: 100%;
    height: 150vh;
    background: #13E8E9;
}
body{
    margin: 0;
    background: #DDDDE0;
}
#root{
    height: 0;
}
