.button_next{
    /* Rectangle 4 */
    position: relative;
    width: 560px;
    height: 64px;
    top: 300px;

    cursor: pointer;
    background: #EEEEEE;
    box-shadow: 0px 4px 64px rgba(0, 0, 0, 0.05);
    border-radius: 8px;

    font-family: Rubik, serif;
    font-style: normal;
    font-weight: bold;
    font-size: 26px;
    line-height: 31px;
    display: flex;
    align-items: center;
    text-align: center;

    color: #000000;
}